import React, { useState } from 'react'
//import NewCalender from './Calender'
//import request from 'superagent'
import { UserContext } from './components/User'

function TestPage(props) {
  const { state, dispatch } = React.useContext(UserContext)
  const [mailMessage, setMailMessage] = useState('')
  React.useEffect(() => {}, [state.user])
  const resetColor = () =>
    dispatch({
      type: 'reset-user',
    })

  // "chnage-color"dispatch
  // payload
  const setColor = color => () =>
    dispatch({
      type: 'set-user',
      payload: {
        user: { username: 'name', userId: 'sampleId', picture: 'http://' },
      },
    })
  const sendMail = () => {
    if (0 >= mailMessage.length) {
      return
    }
    console.log(mailMessage)
    /*
    fetch('test/sendmail', {
      method: 'POST',
      body: JSON.stringify(['message']),
      headers: {
        'Content-Type': 'application/json',
      },
      mode: 'cors',
    })
      .then(res => res.json())
      .then(list => {})
      */
  }
  return (
    <div>
      <h1>TestPage</h1>
      <p></p>
      <ul>
        <li>: {state.user.username}</li>
        <li>: {state.user.userId}</li>
        <li>: {state.user.picture}</li>
      </ul>
      <button
        onClick={setColor({
          name: 'Dark',
          text: '#ffffff',
          back: '#000000',
        })}
      >
        
      </button>
      <button onClick={resetColor}></button>
      <div>
        
        <br />
        <textarea
          value={mailMessage}
          onChange={e => setMailMessage(e.target.value)}
        ></textarea>
      </div>
      <button onClick={sendMail}></button>
    </div>
  )
}
export default TestPage
